<template>
  <b-modal
    class="NDTResetModal"
    :visible="resetModalVisible"
    hide-header
    hide-footer
    centered
    @hidden="reset"
    @shown="focus"
  >
    <b-container class="NDTReset">
      <form @submit.prevent="resetPassword">
        <div class="NDTReset__Header">
          <h1 class="h3 mb-3 font-weight-normal">
            Glemt passord
          </h1>
          <a href="#" @click="hideReset">
            <icon icon="times" />
          </a>
        </div>
        <label for="reset_username" class="sr-only">
          Brukernavn eller E-post
        </label>
        <input
          type="text"
          id="reset_username"
          class="NDTReset__username form-control"
          placeholder="Brukernavn eller E-post"
          required
          autofocus
          ref="username"
          v-model="username"
          autocomplete="username"
          :disabled="resetting || done"
        >

        <br>

        <button
          class="NDTReset__submit btn btn-lg btn-block"
          type="submit"
          :disabled="resetting || done"
        >
          <template v-if="!resetting">
            Tilbakestill
          </template>
          <template v-else>
            <icon icon="spinner" spin />
            <span class="NDTReset__spinnerText">
              Tilbakestiller
            </span>
          </template>
        </button>
      </form>
      <template v-if="done">
        <br>
        <b-alert variant="success" show dismissible>
          Vi har sendt deg en e-post med instruksjoner.
        </b-alert>
      </template>
    </b-container>
  </b-modal>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'

  export default {
    name: 'NDTResetModal',

    data: _ => ({
      username: '',
      resetting: false,
      done: false
    }),

    computed: {
      ...mapGetters({
        resetModalVisible: 'modals/reset',
      })
    },

    methods: {
      ...mapActions({
        hideReset: 'modals/hideReset'
      }),

      reset () {
        this.username = ''
      },

      focus () {
        this.$refs.username.focus()
      },

      async resetPassword () {
        this.resetting = true
        await this.$api.post('reset', {
          username: this.username
        })
        this.resetting = false
        this.done = true
      }
    }
  }
</script>

