export default {
  namespaced: true,

  state: {
    auth: false,
    register: false,
    reset: false
  },

  getters: {
    auth: state => state.auth,
    register: state => state.register,
    reset: state => state.reset
  },

  mutations: {
    SET_AUTH: (state, show) => {
      state.auth = show
    },
    SET_REGISTER: (state, show) => {
      state.register = show
    },
    SET_RESET: (state, show) => {
      state.reset = show
    }
  },

  actions: {
    showAuth: ({ commit }) => {
      commit('SET_AUTH', true)
    },

    hideAuth: ({ commit }) => {
      commit('SET_AUTH', false)
    },

    showRegister: ({ commit }) => {
      commit('SET_REGISTER', true)
    },

    hideRegister: ({ commit }) => {
      commit('SET_REGISTER', false)
    },

    showReset: ({ commit }) => {
      commit('SET_RESET', true)
    },

    hideReset: ({ commit }) => {
      commit('SET_RESET', false)
    }
  }
}
