      </a>
      <a href="#" class="NDTUserMenu__Action" @click="showAuth">
        <icon icon="sign-in-alt" class="NDTUserMenu__Icon" />
        Logg inn
      </a>
    </template>
  </section>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'NDTUserMenu',

    computed: {
      ...mapGetters({
        user: 'user/user'
      })
    },

    methods: {
      ...mapActions({
        showAuth: 'modals/showAuth',
        showRegister: 'modals/showRegister',
        setUser: 'user/setUser',
        hideAuth: 'modals/hideAuth'
      })
    }
  }
</script>

