export default {
  namespaced: true,

  state: {
    sponsors: []
  },

  getters: {
    sponsors: state => state.sponsors
  },

  mutations: {
    SET_SPONSORS: (state, sponsors) => {
      state.sponsors = sponsors
    }
  },

  actions: {
    setSponsors: ({ commit }, sponsors) => {
      commit('SET_SPONSORS', sponsors)
    }
  }
}
