<template>
  <b-modal
    class="NDTAuthModal"
    :visible="authModalVisiable"
    hide-header
    hide-footer
    centered
    @hidden="reset"
    @shown="focus"
  >
    <ndt-auth ref="ndtAuth" />
  </b-modal>
</template>

<script>
  import NdtAuth from '@/components/NdtAuth'
  import { mapGetters } from 'vuex'

  export default {
    name: 'NDTAuthModal',

    components: {
      NdtAuth
    },

    computed: {
      ...mapGetters({
        authModalVisiable: 'modals/auth'
      })
    },

    methods: {
      reset () {
        this.$refs.ndtAuth.reset()
      },

      focus () {
        this.$refs.ndtAuth.focus()
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/scss/variables";

  .NDTAuthModal {
    /deep/ .modal-content {
      background: $dark-gray;
