<template>
  <b-container class="NDTAuth">
    <form @submit.prevent="authenticate">
      <div class="NDTAuth__Header">
        <h1 class="h3 mb-3 font-weight-normal">
          Logg inn
        </h1>
        <a href="#" @click="hideAuth">
          <icon icon="times" />
        </a>
      </div>
      <label for="username" class="sr-only">
        Brukernavn
      </label>
      <input
        type="text"
        id="username"
        class="NDTAuth__username form-control"
        placeholder="Brukernavn"
        required
        autofocus
        v-model="username"
        ref="username"
        autocomplete="username"
        :disabled="authenticating"
      >
      <label
        for="password"
        class="sr-only"
      >
        Passord
      </label>
      <input
        type="password"
        id="password"
        class="NDTAuth__password form-control"
        placeholder="Passord"
        required
        v-model="password"
        autocomplete="current-password"
        :disabled="authenticating"
      >
      <button
        class="NDTAuth__submit btn btn-lg btn-block"
        type="submit"
      >
        <template v-if="!authenticating">
          Logg inn
        </template>
        <template v-else>
          <icon icon="spinner" spin />
          <span class="NDTAuth__spinnerText">
            Logger inn
          </span>
        </template>
      </button>

      <b-form-text class="NDTAuth__help">
        <a href="#" @click="resetPassword">Glemt passord?</a>
      </b-form-text>
    </form>
  </b-container>
</template>

<script>
  import { mapActions } from 'vuex'

  export default {
    name: 'NDTAuth',

    data: _ => ({
      username: '',
      password: '',
      authenticating: false
    }),

    computed: {
      validated () {
        return Boolean(this.username.length && this.password.length)
      }
    },

    methods: {
      ...mapActions({
        setToken: 'auth/setToken',
        hideAuth: 'modals/hideAuth',
        showReset: 'modals/showReset',
      }),

      reset () {
        this.username = ''
        this.password = ''
      },

      focus () {
        this.$refs.username.focus()
      },

      resetPassword () {
        this.hideAuth()
        this.showReset()
      },

      async authenticate () {
        if (!this.authenticating && this.validated) {
          try {
            this.authenticating = true
            const { username, password } = this

            const { id: token } = await this.$api.post('auth', {
              username,
              password
            })

            if (token) {
              this.setToken(token)
            }
          } catch (error) {
            console.error(error)
          } finally {
            this.authenticating = false
          }
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/scss/variables";

  .NDTAuth {
    background: $dark-gray;

    &__Header {
      display: flex;
      justify-content: space-between;
    }

    &__username {
      margin-bottom: -1px;
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;
    }

    &__password {
      margin-bottom: 1.5rem;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }

    &__submit {
      background-color: $lime;
      color: $dark-gray;
    }

    &__spinnerText {
      margin-left: 1rem;
    }

    &__help {
      padding-top: 1rem;
      a {
        margin-right: 1rem;
      }
    }
  }
</style>
