<template>
  <b-container class="NDTRegister">
    <form @submit.prevent="register">
      <div class="NDTRegister__Header">
        <h1 class="h3 mb-3 font-weight-normal">
          Ny bruker
        </h1>
        <a href="#" @click="hideRegister">
          <icon icon="times" />
        </a>
      </div><br>
      <label for="register_username">
        <icon :icon="username && usernameIsValidated ? 'check-circle' : 'circle'" />
        Brukernavn:
      </label>
      <input
        type="text"
        id="register_username"
        class="NDTRegister__username form-control"
        placeholder="Brukernavn"
        required
        autofocus
        v-model="username"
        ref="username"
        :disabled="registering"
      ><br>
      <label for="register_name">
        <icon :icon="name && nameIsValidated ? 'check-circle' : 'circle'" />
        <template v-if="name">

        </template>
        Navn (fornavn + etternavn):
      </label>
      <input
        type="text"
        id="register_name"
        class="NDTRegister__username form-control"
        placeholder="Navn"
        required
        autofocus
        v-model="name"
        autocomplete="name"
        :disabled="registering"
      ><br>
      <label for="register_email">
        <icon :icon="email && emailIsValidated ? 'check-circle' : 'circle'" />
        E-post:
      </label>
      <input
        type="email"
        id="register_email"
        class="NDTRegister__username form-control"
        placeholder="E-post"
        required
        autofocus
        v-model="email"
        autocomplete="email"
        :disabled="registering"
      ><br>
      <label for="register_phone">
        <icon :icon="phone && phoneIsValidated ? 'check-circle' : 'circle'" />
        Telefonnumer:
      </label>
      <input
        type="tel"
        id="register_phone"
        class="NDTRegister__username form-control"
        placeholder="Telefonnummer"
        required
        autofocus
        v-model="phone"
        :disabled="registering"
      ><br>
      <label for="register_dob">
        <icon :icon="dob && dobIsValidated ? 'check-circle' : 'circle'" />
        Fødselsdag:
      </label>
      <input
        type="date"
        id="register_dob"
        class="NDTRegister__username form-control"
        placeholder="Fødselsdag"
        required
        autofocus
        v-model="dob"
        autocomplete="bday"
        :disabled="registering"
      ><br>
      <b-alert variant="warning" :show="age && !isOverMinimumAge">
        Beklager, du må være fylt 13 år for å kunne delta på NDT-LAN
      </b-alert>
      <template v-if="!isOver16">
          <label for="register_parent_name">
            <icon :icon="parent_name && parentNameIsValidated ? 'check-circle' : 'circle'" />
            Navn på forelder:
          </label>
          <input
            type="text"
            id="register_parent_name"
            class="NDTRegister__username form-control"
            placeholder="Navn på foresatt"
            required
            autofocus
            v-model="parent_name"
            :disabled="registering"
          >
          <br>
          <label for="register_parent_phone">
            <icon :icon="parent_phone && parentPhoneIsValidated ? 'check-circle' : 'circle'" />
            Telefonnummer til foresatt:
          </label>
          <input
            type="tel"
            id="register_parent_phone"
            class="NDTRegister__username form-control"
            placeholder="Telefonnummer til foresatt"
            required
            autofocus
            v-model="parent_phone"
            :disabled="registering"
          >
        <br>
      </template>
      <label
        for="register_password"

      >
        <icon :icon="password && passwordIsValidated ? 'check-circle' : 'circle'" />
        Passord:
      </label>
      <input
        type="password"
        id="register_password"
        class="NDTRegister__password form-control"
        placeholder="Passord"
        required
        v-model="password"
        :disabled="registering"
      >
      <label
        for="register_password_repeat"
      >
        <icon :icon="password_repeat && passwordRepeatValidated ? 'check-circle' : 'circle'" />
        Gjenta Passord:
      </label>
      <input
        type="password"
        id="register_password_repeat"
        class="NDTRegister__password form-control"
        placeholder="Gjenta passord"
        required
        v-model="password_repeat"
        :disabled="registering"
      >
      <template v-if="error">
        <b-alert variant="danger" show>
          {{ error }}
        </b-alert>
        <br>
      </template>
      <button
        class="NDTRegister__submit btn btn-lg btn-block"
        type="submit"
        :disabled="!formIsValidated"
      >
        <template v-if="!registering">
          Registrer
        </template>
        <template v-else>
          <icon icon="spinner" spin />
          <span class="NDTRegister__spinnerText">
            Registrerer
          </span>
        </template>
      </button>
    </form>
  </b-container>
</template>

<script>
  import { mapActions } from 'vuex'
  import moment from 'moment'

  const emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

  export default {
    name: 'NDTRegister',

    data: _ => ({
      name: '',
      username: '',
      password: '',
      password_repeat: '',
      email: '',
      dob: '',
      phone: '',
      parent_name: '',
      parent_phone: '',
      registering: false,
      error: null
    }),

    computed: {
      formIsValidated () {
        return Boolean((this.username && this.usernameIsValidated)
          && (this.name && this.nameIsValidated)
          && (this.email && this.emailIsValidated)
          && (this.phone && this.phoneIsValidated)
          && (this.dob && this.dobIsValidated)
          && (
            !this.isOverMinimumAge ? (
              (this.parent_name && this.parentNameIsValidated)
              && (this.parent_phone && this.parentPhoneIsValidated)
            ) : true
          ) && (this.password && this.passwordIsValidated)
          && (this.password_repeat && this.passwordRepeatValidated))
      },

      usernameIsValidated () {
        if (this.username) {
          return this.username.length >= 3
        }

        return true
      },

      nameIsValidated () {
        if (this.name) {
          const parts = this.name.split(' ')
          return this.name.length > 3 && parts.length >= 2 && parts[parts.length - 1].length >= 2
        }

        return true
      },

      emailIsValidated () {
        if (this.email) {
          return emailRegex.test(this.email)
        }

        return true
      },

      dobIsValidated () {
        return this.isOverMinimumAge && this.isBelowMaxAge
      },

      passwordIsValidated () {
        if (this.password) {
          return this.password.length > 6
        }
      },

      passwordRepeatValidated () {
        if (this.password && this.password_repeat) {
          return this.password_repeat === this.password
        }
      },

      phoneIsValidated () {
        return this.phone.length >= 8
      },

      parentNameIsValidated () {
        if (this.parent_name) {
          const parts = this.parent_name.split(' ')
          return this.parent_name.length > 3 && parts.length >= 2 && parts[parts.length - 1].length >= 2
        }

        return true
      },

      parentPhoneIsValidated () {
        if (this.parent_phone) {
          return this.parent_phone.length >= 8
        }

        return true
      },

      age () {
        if (this.dob) {
          const now = moment()
          const dob = moment(this.dob)
          return now.diff(dob, 'years')
        }

        return 0
      },

      isOverMinimumAge () {
        return this.age >= 13
      },

      isBelowMaxAge () {
        return this.age <= 100
      },

      isOver16 () {
        if (this.age) {
          return this.age >= 16
        }

        return true
      }
    },

    methods: {
      ...mapActions({
        setToken: 'auth/setToken',
        hideRegister: 'modals/hideRegister',
      }),

      reset () {
        this.username = ''
        this.password = ''
      },

      focus () {
        this.$refs.username.focus()
      },

      async register () {
        this.registering = true
        this.error = null
        if (this.formIsValidated) {
          const form = {
            name: this.name,
            username: this.username,
            phone: this.phone,
            email: this.email,
            dob: this.dob,
            parent_name: this.parent_name,
            parent_phone: this.parent_phone,
            password: this.password
          }

          try {
            await this.$api.post('register', form)
            this.hideRegister()
          } catch (error) {
            switch (error.reason) {
              case 'user_exists':
                this.error = 'Denne brukeren finnes allerede'
                break
              default:
                this.error = 'Det oppstod en ukjent feil. Vennligst forsøk igjen'
                break
            }
          }
          this.registering = false
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/scss/variables";

  .NDTRegister {
    background: $dark-gray;

    &__Header {
      display: flex;
      justify-content: space-between;
    }

    &__username {
      margin-bottom: -1px;
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;
    }

    &__password {
      margin-bottom: 1.5rem;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }

    &__submit {
      background-color: $lime;
      color: $dark-gray;
    }

    &__spinnerText {
      margin-left: 1rem;
    }

    &__help {
      padding-top: 1rem;
      a {
        margin-right: 1rem;
      }
    }
  }
</style>
