export default {
  namespaced: true,

  state: {
    types: [],
    selected: null
  },

  getters: {
    types: state => state.types,
    selected: (state, _, __, rootGetters) => {
      if (rootGetters['seatmap/mySeat']) {
        if (state.types.find(type => type.id === state.selected)) {
          return state.selected
        }
      }

      return null
    }
  },

  mutations: {
    SET_TICKET_TYPES: (state, types) => {
      state.types = types
    },
    SET_SELECTED_TICKET: (state, ticket) => {
      state.selected = ticket
    }
  },

  actions: {
    setTicketTypes: ({ commit }, types) => {
      commit('SET_TICKET_TYPES', types)
    },

    selectTicket: ({ commit }, ticket) => {
      commit('SET_SELECTED_TICKET', ticket)
    }
  }
}
