
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import Icon from '@/components/Icon'
import BootstrapVue from 'bootstrap-vue'
import redirect from '@/utils/redirect'

import config from '@/config.json'
import ApiClient from '@/utils/ApiClient'
import VueStripeCheckout from 'vue-stripe-checkout'

import '@/assets/scss/main.scss'

Vue.use(BootstrapVue)
Vue.component('icon', Icon)
Vue.config.productionTip = false

function getApiConfig () {
  if (window.webpackHotUpdate || window.location.host !== 'ndt-lan.no') {
    return {
      url: config.dev_apiURL,
      token: config.dev_apiToken,
      stripeKey: config.dev_stripeKey
    }
  }
  return {
    url: config.apiURL,
    token: config.apiToken,
    stripeKey: config.stripeKey
  }
}

const apiConfig = getApiConfig()

Vue.use(VueStripeCheckout, {
  key: apiConfig.stripeKey,
  image: 'https://d1i137u4q32ft6.cloudfront.net/media/rc/200x200/1518232527/ndt-black-facebook.png',
  locale: 'no',
  currency: 'NOK',
  billingAddress: false,
  panelLabel: 'Betal {{amount}}'
})

Vue.prototype.$api = new ApiClient(apiConfig.url, {
  redirect: 'follow',
  headers: {
    'Authorization': `Bearer ${apiConfig.token}`
  }
})

const app = new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

redirect(window.location, url => app.$router.replace(url))
