<template>
  <b-container>
    <div class="App">
      <ndt-navbar class="App__Navbar" />

      <ndt-user-menu class="App_UserMenu" />

      <div class="App__Content">
        <router-view :key="$route.url" />
      </div>

      <ndt-footer class="App__Footer" />
    </div>

    <ndt-auth-modal />
    <ndt-register-modal />
    <ndt-reset-modal />
  </b-container>
</template>

<script>
  import NdtNavbar from '@/components/NdtNavbar'
  import NdtFooter from '@/components/NdtFooter'
  import NdtAuthModal from '@/components/NdtAuthModal'
  import NdtRegisterModal from '@/components/NdtRegisterModal'
  import NdtResetModal from '@/components/NdtResetModal'
  import NdtUserMenu from '@/components/NdtUserMenu'

  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'App',

    components: {
      NdtNavbar,
      NdtFooter,
      NdtAuthModal,
      NdtUserMenu,
      NdtRegisterModal,
      NdtResetModal,
    },

    methods: {
      ...mapActions({
        'showAuth': 'modals/showAuth',
        'hideAuth': 'modals/hideAuth',
        'setUser': 'user/setUser',
        'setSignups': 'user/setSignups',
        'setToken': 'auth/setToken'
      }),

      async authenticate () {
        if (this.token) {
          try {
            const user = await this.$api.get(`auth?user=${this.token}`)
            const signups = await this.$api.get(`signups?user=${this.token}`)
            this.setUser(user)
            this.setSignups(signups)
            this.hideAuth()
          } catch (error) {
            this.setUser(null)
            this.setToken(null)
          } finally {

          }
        }
      }
    },

    computed: {
      ...mapGetters({
        user: 'user/user',
        token: 'auth/token'
      })
    },

    watch: {
      'token': 'authenticate'
    },

    async mounted () {
      this.authenticate()
    }
  }
</script>

<style lang="scss" scoped>
  .App {
    display: grid;
    min-height: 100vh;
    grid-template-columns: 100%;
    grid-template-rows: auto auto 1fr auto;
    grid-template-areas: 'navbar' 'user-menu' 'content' 'footer';

    &__Navbar {
      grid-area: 'navbar';
    }

    &__UserMenu {
      grid-area: 'user-menu';
    }

    &__Content {
      margin-top: 1.5rem;
      grid-area: 'content';
    }

    &__Footer {
      grid-area: 'footer';
    }
  }
</style>
