export default {
  namespaced: true,

  state: {
    token: null
  },

  getters: {
    token: state => state.token
  },

  mutations: {
    SET_TOKEN: (state, token = null) => {
      state.token = token
    }
  },

  actions: {
    setToken: ({ commit }, token) => {
      commit('SET_TOKEN', token)
    },

    clearToken: ({ commit }) => {
      commit('SET_TOKEN', null)
    }
  }
}
