export default {
  namespaced: true,

  state: {
    user: null,
    signups: []
  },

  getters: {
    user: state => state.user,
    signups: state => state.signups,
    hasSignup: (state, _, __, rootGetters) => {
      const event = rootGetters['events/current']
      if (event) {
        const signup = state.signups.find(signup => Number(signup.entry_id) === event.id)
        if (signup) {
          return signup.code
        }
      }

      return false
    }
  },

  mutations: {
    SET_USER: (state, user) => {
      state.user = user
    },

    SET_SIGNUPS: (state, signups = []) => {
      state.signups = signups
    }
  },

  actions: {
    setUser: ({ commit }, user) => {
      commit('SET_USER', user)
    },

    setSignups: ({ commit }, signups = []) => {
      commit('SET_SIGNUPS', signups)
    }
  }
}
